.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 20px;
  color: white;
  vertical-align: middle;
}
.App-info {
  background-color: #222;
  height: 50px;
  width: 20%;
  padding: 20px;
  color: white;
  display: inline-block;
  vertical-align: middle;
  -ms-flex-line-pack: start;
      align-content: flex-start;

}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

button {
  width: 120px;
  margin: 60px;
  font-weight: 700;
  color: white;
  text-decoration: none;
  padding: .8em 1em calc(.8em + 3px);
  border-radius: 3px;
  background: rgb(64,199,129);
  -webkit-box-shadow: 0 -3px rgb(53,167,110) inset;
          box-shadow: 0 -3px rgb(53,167,110) inset;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
button:hover {
  background: rgb(53, 167, 110);
}
button:active {
  background: rgb(33,147,90);
  -webkit-box-shadow: 0 3px rgb(33,147,90) inset;
          box-shadow: 0 3px rgb(33,147,90) inset;
}

#BackButton {
  height: 60px;
  width: 120px;
  border-radius: 50%;
}




